import React, {useEffect } from 'react'
import "../style/Home.css"
import gsap from 'gsap'
import ScrollTrigger from 'gsap/ScrollTrigger'
import ContainerOne from '../components/ContainerOne';
import ContainerTwo from '../components/ContainerTwo';


gsap.registerPlugin(ScrollTrigger);




const Home = () => {

  useEffect(()=>{

    gsap.to("body", {
      css: {
        visibility: "visible"
      }
    })

    const containerOneTimeline = gsap.timeline()
    
    containerOneTimeline.from(".container.first .content .head",{
      y : 100,
      opacity: 0,
      duration: 1,
      ease: "power4.out",
  
    }
    )  
  
    .from(".container.first .content .writeup",{
      y : 100,
      opacity: 0,
      duration: 1,
      ease: "power4.out",
  
    }, "-=0.5"
    )
 
    .to(".container.first",
    {
        css:{
          backgroundColor : "#041C32",
        },
        scrollTrigger:{
          trigger: ".container.first",
          start: "bottom bottom",
          duration: 5,
          end: "bottom 60%",
          toggleActions:"restart pause reverse pause",
          scrub: 1,
          // end : ()=> "+=1000"
        },
        }
    )
    .to(".container.first",
        {
           
            scrollTrigger:{
              trigger: ".container.first",
              start: "bottom bottom",
              duration: 5,
              end: "bottom top",
              toggleActions:"restart pause reverse pause",
              scrub: 1,
              pin: true,
              // end : ()=> "+=1000"
            },
            }
        )
    
    .to(".container.first .content",
    {
        y: -100,
        // opacity: 0,
        scrollTrigger:{
          trigger: ".container.first",
          start: "bottom bottom",
          duration: 5,
          end: "bottom top",
          toggleActions:"restart pause reverse pause",
          scrub: 1,
          pin: true,
          // end : ()=> "+=1000"
        },
        }
    )

    gsap.to(".container.first .image .day",
    {   css:{
      visibility: "hidden",
    },
        scrollTrigger:{
          trigger: ".container.dummy.one",
          start: "top 75%",
          end: "top 25%",
          toggleActions:"restart pause reverse pause",
          scrub: 1,
          // end : ()=> "+=1000"
        },
        }
    )
    
    
    
     
    },[])


  return (
    <div className="maincontainer-home">
     
    <ContainerOne/>
    <div className='container dummy one'>

    </div>
    <ContainerTwo/>
      
    
    </div>
  )
}

export default Home


  





  